.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}
@font-face {
  font-family: "PT Sans";
  font-weight: 700;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff2") format("woff");
}
@font-face {
  font-family: "PT Sans";
  font-weight: 400;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff2") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.button {
  background-color: #81b3d2;
  transition: all 0.3s;
}
.button:hover {
  background-color: #669ec0;
}
.button:active {
  background-color: #5496bd;
}
.button__text {
  color: #ffffff;
  transition: all 0.3s;
}
.button__text:active {
  opacity: 0.3;
}
.button--disabled {
  background-color: #e5e5e5;
}
.button--disabled:hover,
.button--disabled:active {
  background-color: #e5e5e5;
}
.invisible {
  display: none;
}
.page {
  height: 100%;
}
.page__body {
  min-height: 100%;
  width: 320px;
  margin: 0 auto;
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 14px;
  line-height: 26px;
  font-weight: normal;
  color: #000000;
  background-color: #ffffff;
  text-transform: uppercase;
  display: flex;
  flex-direction: column;
  text-align: center;
}
.page__main {
  margin-top: 55px;
}
@media (min-width: 768px) {
  .page__body {
    width: 768px;
  }
  .page__header {
    position: relative;
  }
  .page__main {
    margin-top: 0;
  }
}
@media (min-width: 1200px) {
  .page {
    background-color: #f2f2f2;
  }
  .page__body {
    width: 1200px;
    box-shadow: 0 5px 15px rgba(0, 1, 1, 0.2);
  }
}
.main-nav {
  position: relative;
}
.main-nav__button {
  position: absolute;
  right: 15px;
  top: 18px;
  margin: 0;
  padding: 0;
  display: block;
  background-color: transparent;
  border: none;
  outline: none;
  cursor: pointer;
}
.main-nav__button:hover {
  opacity: 0.6;
}
.main-nav__button:active {
  opacity: 0.3;
}
.main-nav__icon-menu {
  color: #81b3d2;
  display: block;
}
.main-nav__icon-cross {
  color: #ffffff;
  display: none;
}
.main-nav__button--open .main-nav__icon-menu {
  display: none;
}
.main-nav__button--open .main-nav__icon-cross {
  display: block;
}
.main-nav__wrapper {
  display: none;
}
.main-nav__wrapper--open {
  display: block;
}
.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.main-nav__item {
  padding-top: 10px;
  padding-bottom: 6px;
  background-color: #81b3d2;
  box-shadow: 0px 1px 0px rgba(1, 1, 1, 0.2), inset 0px 1px 0px rgba(255, 255, 255, 0.2);
  transition: all 0.3s;
}
.main-nav__item:hover {
  background-color: #669ec0;
}
.main-nav__item:active {
  background-color: #5496bd;
}
.main-nav__link {
  text-decoration: none;
  color: #ffffff;
  font-size: 18px;
  line-height: 40px;
  font-weight: bold;
  transition: all 0.3s;
}
.main-nav__link:active {
  opacity: 0.3;
}
.main-nav__logo {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}
@media (min-width: 768px) {
  .main-nav__button {
    display: none;
  }
  .main-nav__wrapper {
    display: block;
  }
  .main-nav__list {
    display: flex;
    flex-wrap: wrap;
  }
  .main-nav__item {
    background-color: #ffffff;
    box-shadow: none;
    width: 153px;
    padding-top: 16px;
    padding-bottom: 14px;
  }
  .main-nav__item:nth-child(2) {
    margin-right: 156px;
  }
  .main-nav__item:hover {
    background-color: #ffffff;
  }
  .main-nav__item:active {
    background-color: #ffffff;
  }
  .main-nav__link {
    color: #000000;
    font-size: 14px;
    line-height: 26px;
  }
  .main-nav__link:hover {
    color: #81b3d2;
  }
  .main-nav__link:active {
    color: #000000;
  }
  .main-nav__link--active {
    color: #766357;
  }
  .main-nav__link--active:hover {
    color: #604e43;
  }
  .main-nav__link--active:active {
    color: #766357;
  }
  .main-nav__logo {
    top: 0;
  }
}
@media (min-width: 1200px) {
  .main-nav__item {
    width: 239px;
  }
  .main-nav__item:nth-child(2) {
    margin-right: 244px;
  }
}
